::selection {
	@apply bg-white text-blue;
}

::-webkit-scrollbar {
	width: 8px;
}

::-webkit-scrollbar-thumb {
	@apply bg-white;
	border-radius: 0.8rem;
	transition: 0.3s background-color;
}

::-webkit-scrollbar-track {
	border-left: 3px solid;
	@apply bg-blue border-blue;
}

[data-scroll-container] {
	overflow-x: hidden;
	padding: 0;
	width: 100%;
	//transition: 1s all;
}

[data-scroll-section] {
	perspective: 1px;
	will-change: transform;
}

.rtl {
	direction: rtl;
}

.c-scrollbar {
	height: 100vh;
	z-index: 60;

	.c-scrollbar_thumb {
		@apply bg-white opacity-80;
	}

	+ div {
		z-index: 1000;
		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			width: 60%;
			transition: 0.3s all;
		}
	}
}

figure {
	img,
    video {
		@include img-cover;
	}
}