.hero {

    hr {
        color: #b1b1b1;
    }

    .name {
        img {
            border-radius: 0.5rem 0.5rem 0 0;
        }

        span {
            display: block;
            font-size: 75%;
            font-weight: normal;
            text-transform: capitalize;
        }
    }

    .details {

        p {
            margin: 0;
        }
    }

    &[data-team="yellow"] {
        .details {
            > div:first-child {
                @apply text-blue;
            }
        }
    }

    .mods {
        > div {
            display: flex;
            align-items: center;
            justify-content: space-between;
            box-shadow: 
            1px 0 0 0 #b1b1b1,
            0 1px 0 0 #b1b1b1,
            1px 1px 0 0 #b1b1b1,   /* Just to fix the corner */
            1px 0 0 0 #b1b1b1 inset,
            0 1px 0 0 #b1b1b1 inset;
        }

        svg {
            fill: currentColor;
            opacity: 0.5;
        }
    }

    svg {
        fill: #fff;
        height: 1em;
        width: 1em;
        margin: 0 1rem;
    }

}